import React from 'react';
import {
    LinearProgress,
    Box,
    makeStyles
} from '@material-ui/core';
import Lottie from 'react-lottie';
import LoadingAnimation3d from '../lotties/3d-loading-animation.json';

const useStyles = makeStyles((theme) => ({
    root: {
        height: "100vh",
        width: "100vw",
        overflowX: "hidden",
        background: "linear-gradient(#493240, #FF0099)"
    }
}))

function Loading() {

    const styles = useStyles();

    const defaultOptions = {
        loop: true,
        autoplay: true, 
        animationData: LoadingAnimation3d,
        rendererSettings: {
            preserveAspectRatio: 'xMidYMid slice'
        }
    };

    return (
        <Box display="flex" flexDirection="column" className={styles.root}>
            <LinearProgress color="secondary"/>

            <Box display="flex" flexGrow={1} flexDirection="column" justifyContent="center" alignItems="center">
                <h1 style={{color: "#fff"}}>Who's the birthday girl...</h1>
                <Lottie options={defaultOptions} height={200} width={200}/>
            </Box>
            
        </Box>
    )
}

export default Loading;