import React from 'react';
import {
    makeStyles,
    Box,
    Grid
} from '@material-ui/core';
import clsx from 'clsx';
import Lottie from 'react-lottie';
import createOptions from '../core/createOptions';

import confetti1 from '../lotties/confetti-1.json';
import confetti from '../lotties/confetti.json';
import heartburst from '../lotties/heart-burst.json';
import burstInPlace from '../lotties/burst-in-place.json';
import birthdayCake from '../lotties/birthday-cake.json';
import present from '../lotties/present.json';
import smileyFruit from '../lotties/smiley-fruit.json';
import warpSpeedRing from '../lotties/warp-speed-ring.json';
import gradientLoader from '../lotties/gradient-loader-01.json';
import beatingAudioAmplifiers from '../lotties/beating-audio-amplifiers.json';
import playing from '../lotties/playing.json';
import winner from '../lotties/winner.json';
import place from '../lotties/place.json';
import love1 from '../lotties/love-1.json';
import love2 from '../lotties/love-2.json';
import love3 from '../lotties/love-3.json';
import love4 from '../lotties/love-4.json';

import picture1 from '../picture/picture-1.jpg';
import picture2 from '../picture/picture-2.jpg';
import picture3 from '../picture/picture-3.jpg';
import picture4 from '../picture/picture-4.jpg';
import picture5 from '../picture/picture-5.jpg';

import video1 from '../picture/video-1.mp4';
import video2 from '../picture/video-2.mp4';
import video3 from '../picture/video-3.mp4';
import video4 from '../picture/video-4.mp4';
import video5 from '../picture/video-5.mp4';
import video6 from '../picture/video-6.mp4';
import video7 from '../picture/video-7.mp4';
import video9 from '../picture/video-9.mp4';
import video10 from '../picture/video-10.mp4';


const useStyles = makeStyles((theme) => ({
    root: {
        minHeight: "100vh",
        width: "100vw",
        overflowX: "hidden",
        background: "linear-gradient(#493240 ,#FF0099)"
    },

    titleHolder: {
        background: "rgba(255,255,255, 0.45)",
        borderRadius: 10,
        margin: 10,
        padding: 10,
        textAlign: "center"
    },

    guha: {
        background: "rgba(242,82,51, 0.45)",
        borderRadius: 10,
        margin: 10,
        padding: 10,
        textAlign: "center"
    },


    para: {
        background: "#fff",
        borderRadius: 10,
        margin: 10,
        padding: 10,
        textAlign: "center"
    }
}))

export default function Home() {
    
    const styles = useStyles();
    
    return (
        <Box className={styles.root}>

            <Box className={clsx(styles.titleHolder, "animate__animated", "animate__fadeInDown")}>
                <h1>HAPPY BIRTHDAY SUE JAAN ❤️</h1>
            </Box>

            <Box className={clsx(styles.titleHolder, "animate__animated", "animate__fadeInDown", "animate__delay-1s")}
                display="flex" justifyContent="space-between"
            >
                <Lottie height={200} width={200} options={createOptions(confetti)} />
                <Lottie height={200} width={200} options={createOptions(heartburst)} />
                <img src={picture1} width={380} style={{borderRadius: 10}} />
                <Lottie height={200} width={200} options={createOptions(heartburst)} />
                <Lottie height={200} width={200} options={createOptions(confetti)} />
            </Box>

            <p style={{textAlign: "center", fontSize: 24}}>Happy birthday to the prettiest girl I know</p>

            <Box className={clsx("animate_animated", "animate__fadeInDown", "animate__delay-2s")}
                display="flex" justifyContent="space-between"
            >
                <Lottie height={200} width={200} options={createOptions(present)} />
                <Lottie height={200} width={200} options={createOptions(burstInPlace)} />
                <Lottie height={200} width={200} options={createOptions(present)} />

            </Box>

            <Box className={clsx(styles.titleHolder, "animate__animated", "animate__fadeInDown", "animate__delay-1s")}
                
            >      
                <Box display="flex" justifyContent="center" alignItems="center" flexWrap="wrap">
                    <Lottie height={200} width={200} options={createOptions(confetti1)} />
                    <Box>
                        <div>
                            <img src={picture2} width={380} style={{objectFit: "cover", borderRadius: 10}} />
                        </div>
                        <p style={{fontSize: 24, padding: 10}}>Like honestly I can stare at you all day 😍</p>
                    </Box>
                    <Lottie height={200} width={200} options={createOptions(confetti1)} />
                </Box>

            </Box>

            {/* MOOSIC */}
            <Box className={clsx(styles.titleHolder, "animate__animated", "animate__fadeInDown", "animate__delay-1s")}
        
            >   
                <Box display="flex" justifyContent="center" alignItems="center" flexWrap="wrap"> 
                    <Lottie height={200} width={200} options={createOptions(playing)} />
                    <Box>
                        <div>
                            <video width="380" controls style={{borderRadius: 10}}>
                                <source src={video1} type="video/mp4"/>
                            </video>
                        </div>
                        <p style={{fontSize: 24, padding: 10}}>And listen to you sing all day 😍</p>
                    </Box>
                    <Lottie height={200} width={200} options={createOptions(playing)} />
                </Box>

                <Box display="flex" justifyContent="center" alignItems="center" flexWrap="wrap" marginTop={5}> 
                    <Lottie height={200} width={200} options={createOptions(beatingAudioAmplifiers)} />
                    <Box>
                        <div>
                            <video width="380" controls style={{borderRadius: 10}}>
                                <source src={video2} type="video/mp4"/>
                            </video>
                        </div>
                        <p style={{fontSize: 24, padding: 10}}>You’re my favourite artist</p>
                    </Box>
                    <Lottie height={200} width={200} options={createOptions(beatingAudioAmplifiers)} />
                </Box>

                <Box display="flex" justifyContent="center" alignItems="center" flexWrap="wrap" marginTop={5}> 
                    <Lottie height={200} width={200} options={createOptions(winner)} />
                    <Box>
                        <div>
                            <video width="380" controls style={{borderRadius: 10}}>
                                <source src={video3} type="video/mp4"/>
                            </video>
                        </div>
                        <p style={{fontSize: 24, padding: 10}}>Honestly how talented can someone be</p>
                    </Box>
                    <Lottie height={200} width={200} options={createOptions(winner)} />
                </Box>

            </Box>

            {/* The first meeting place  */}
            <Box display="flex" justifyContent="center" alignItems="center" flexWrap="wrap">
                <Lottie height={200} width={200} options={createOptions(place)} />

                <Box className={clsx("animate_animated", "animate__fadeInDown", "animate__delay-2s")}
                >
                    <img src={picture3} width={380} style={{borderRadius: 10}} />

                    <p style={{fontSize: 24, padding: 10}}>Remember the first time we met?</p>

                </Box>
                <Lottie height={200} width={200} options={createOptions(place)} />

            </Box>

            {/* Best days of rahd's life */}
            <Box className={clsx(styles.titleHolder, "animate__animated", "animate__fadeInDown", "animate__delay-3s")}
                
            >      
                <Box display="flex" justifyContent="center" alignItems="center" flexWrap="wrap">
                    <Lottie height={200} width={200} options={createOptions(love1)} />
                    <Box>
                        <div>
                            <video width="380" controls style={{borderRadius: 10}}>
                                <source src={video4} type="video/mp4"/>
                            </video>
                        </div>
                    </Box>
                    <Lottie height={200} width={200} options={createOptions(love1)} />
                </Box>

                <Box display="flex" justifyContent="center" alignItems="center" flexWrap="wrap">
                    <Lottie height={200} width={200} options={createOptions(love2)} />
                    <Box>
                        <div>
                            <video width="380" controls style={{borderRadius: 10}}>
                                <source src={video5} type="video/mp4"/>
                            </video>
                        </div>
                    </Box>
                    <Lottie height={200} width={200} options={createOptions(love2)} />
                </Box>

                <Box display="flex" justifyContent="center" alignItems="center" flexWrap="wrap">
                    <Lottie height={200} width={200} options={createOptions(love3)} />
                    <Box>
                        <div>
                            <video width="380" controls style={{borderRadius: 10}}>
                                <source src={video6} type="video/mp4"/>
                            </video>
                        </div>
                    </Box>
                    <Lottie height={200} width={200} options={createOptions(love3)} />
                </Box>

                <Box display="flex" justifyContent="center" alignItems="center" flexWrap="wrap">
                    <Lottie height={200} width={200} options={createOptions(love4)} />
                    <Box>
                        <div>
                            <video width="380" controls style={{borderRadius: 10}}>
                                <source src={video7} type="video/mp4"/>
                            </video>
                        </div>
                        <p style={{fontSize: 24, padding: 10}}>One of the best days of my life</p>
                    </Box>
                    <Lottie height={200} width={200} options={createOptions(love4)} />
                </Box>

            </Box>

            <Box className={clsx("animate_animated", "animate__fadeInDown", "animate__delay-2s")}
                display="flex" justifyContent="space-between"
            >
                <Lottie height={200} width={200} options={createOptions(birthdayCake)} />
                <Lottie height={200} width={200} options={createOptions(burstInPlace)} />
                <Lottie height={200} width={200} options={createOptions(birthdayCake)} />

            </Box>

            {/* Guha days */}
            <Box className={clsx(styles.guha, "animate__animated", "animate__fadeInDown", "animate__delay-3s")}
                
            >      
                <Box display="flex" justifyContent="center" alignItems="center" flexWrap="wrap">
                    <Lottie height={200} width={200} options={createOptions(love1)} />
                    <Box>
                        <div>
                            <video width="380" controls style={{borderRadius: 10}}>
                                <source src={video9} type="video/mp4"/>
                            </video>
                        </div>
                    </Box>
                    <Lottie height={200} width={200} options={createOptions(love1)} />
                </Box>

                <Box display="flex" justifyContent="center" alignItems="center" flexWrap="wrap">
                    <Lottie height={200} width={200} options={createOptions(love2)} />
                    <Box>
                        <div>
                            <video width="380" controls style={{borderRadius: 10}}>
                                <source src={video10} type="video/mp4"/>
                            </video>
                        </div>
                        <p style={{fontSize: 24, padding: 10}}>And that day at guha 😏</p>
                    </Box>
                    <Lottie height={200} width={200} options={createOptions(love2)} />
                </Box>

                

            </Box>

            {/* Paragraph */}
            <Box className={clsx(styles.para, "animate__animated", "animate__fadeInDown", "animate__delay-3s")}
                
            >      
                <Box display="flex" justifyContent="center" alignItems="center" flexWrap="wrap">
                    <Lottie height={200} width={200} options={createOptions(smileyFruit)} />
                    <div>
                        <p style={{fontSize: 24, padding: 10}}>
                        Happy birthday jaan. I’m so sorry for that dickhead move but as I said, I’ll make it up to you 😏. You’re the most interesting person I know and god I love talking to you. Can’t wait to prove you wrong 6 years later 😏. We‘ll do all the things in our bucket list, including that guitar thing you’ve always wanted. I’ll eventually get that good with a teacher like you 😏. Happy birthday once again baby
                        </p>
                    </div>
                </Box>
                

            </Box>

            {/* Meme */}

            <Box className={clsx(styles.titleHolder, "animate__animated", "animate__fadeInDown", "animate__delay-1s")}
        
            >   
                <Box display="flex" justifyContent="center" alignItems="center" flexWrap="wrap"> 
                    <Lottie height={200} width={200} options={createOptions(warpSpeedRing)} />
                    <Box>
                        <div>
                            <img src={picture4} width={380} style={{objectFit: "cover", borderRadius: 10}} />
                        </div>
                        <p style={{fontSize: 24, padding: 10}}>When I saw you I said Mashallah 😍</p>
                    </Box>
                    <Lottie height={200} width={200} options={createOptions(warpSpeedRing)} />
                </Box>

                <Box display="flex" justifyContent="center" alignItems="center" flexWrap="wrap" marginTop={5}> 
                    <Lottie height={200} width={200} options={createOptions(gradientLoader)} />
                    <Box>
                        <div>
                            <img src={picture5} width={380} style={{objectFit: "cover", borderRadius: 10}} />
                        </div>
                        <p style={{fontSize: 24, padding: 10}}>Now I say Inshallah 💍</p>
                    </Box>
                    <Lottie height={200} width={200} options={createOptions(gradientLoader)} />
                </Box>
            </Box>


            


        </Box>
    )
}
